import React from "react";
import "../style/CourseGridComponentStyle.css"
import "../images/generic-text-file.jpg"
import courseService from "../services/CourseService";
import {Link} from "react-router-dom";
import cardImage from '../images/generic-text-file.jpg'


export default class CourseCardComponent extends React.Component {

    state = {
        editing: false,
        course: this.props.course,
    }

    setEditing = (editing) => {
        this.setState({editing: editing})
    }

    ok = () =>
        courseService.updateCourse(
            this.state.course._id,
            this.state.course)
            .then(() => this.setEditing(false))

    updateCourseTitle = (newTitle) =>
        this.setState(prevState => ({
            course: {
                ...prevState.course,
                title: newTitle
            }
        }))


    render() {
        return (
            <div className="col-sm-4">
                <div className="card wbdv-card-spacing">
                    <img className="card-img-top"
                         src={cardImage}
                         alt={"No image found"}/>
                    <div className="card-body">

                        {
                            !this.state.editing &&
                            <div>
                                <Link to={`/editor/${this.state.course._id}`}>
                                    <h5 className="card-title">{this.state.course.title}</h5>
                                </Link>
                                <div className="card-text row">
                                    <div className={"col-2"}>
                                        <i className={"fa fa-file-text"}/>
                                    </div>

                                    <div className={"col-8"}>
                                        {this.state.course.modified}
                                    </div>

                                    <button className="col-2 wbdv-card-select"
                                            onClick={this.setEditing}>
                                        <i className={"fa fa-ellipsis-v"}/>
                                    </button>
                                </div>
                            </div>
                        }
                        {
                            this.state.editing &&
                            <div>
                                <div className={"row"}>
                                    <input
                                        className="form-control col-8"
                                        onChange={(event) => this.updateCourseTitle(event.target.value)}
                                        value={this.state.course.title}/>

                                    <button className={"btn wbdv-confirm-change wbdv-card-confirm-change col-2"}
                                            onClick={this.ok}>
                                        <i className={"fa fa-check fa-2x"}/>
                                    </button>
                                    <button
                                        className={"btn wbdv-delete-row wbdv-card-delete col-2"}
                                        onClick={
                                            () => this.props.deleteCourse(this.props.course)}>
                                        <i className={"fa fa-trash fa-2x"}/>
                                    </button>
                                </div>

                            </div>
                        }


                    </div>
                </div>
            </div>


        )
    }
}