import React from "react";
import {Link} from "react-router-dom";

const RegisterComponent = () =>
    <div className="container">
        <div>
            {
                //<!--Header-->
            }
            <div className="row">
                <h1 className="col-6">
                    Sign Up
                </h1>
                <div className="col-6">
                    <Link to={'/'}
                          className="wbdv-cancel-link float-right">
                        Cancel
                    </Link>
                </div>
            </div>

            {
                //<!--Username-->
            }
            <div className="form-group row">
                <label htmlFor="username"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Username
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <input className="form-control wbdv-field wbdv-username"
                           id="username"
                           placeholder="arizzo1234"/>
                </div>
            </div>

            {
                //<!--Password-->
            }
            <div className="form-group row">
                <label htmlFor="password"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Password
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <input type="password"
                           className="form-control wbdv-field wbdv-password"
                           id="password"
                           placeholder="123qWe!@#"/>
                </div>
            </div>

            {
                //<!--Verify Password-->
            }
            <div className="form-group row">
                <label htmlFor="password"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Verify Password
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <input type="password"
                           className="form-control wbdv-field wbdv-password-verify"
                           id="verify-password"
                           placeholder="123qWe!@#"/>
                </div>
            </div>

            {
                //<!--Action Items-->
            }
            <div className="form-group row">
                <label className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    {
                        //<!--Empty-->
                    }
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    {
                        //<!--Register-->
                    }
                    <Link to={'/profile'}
                          className="btn btn-block btn-primary wbdv-button wbdv-register">
                        Register

                    </Link>

                    <div className="row">
                        {
                            //<!--Log in-->
                        }
                        <div className="col-6 wbdv-link wbdv-login">
                            <Link to={'/login'}
                                  className="float-left wbdv-link wbdv-login">
                                Already have an account?
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>


export default RegisterComponent