import React from "react";
import "../style/CourseEditorComponentStyle.css"

const SavePreviewWidgetComponent = () =>
    <div>
        <div className="row">
            <div className="buttons-in-main">
                <div className="float-right h-100">
                    <i className="fa fa-toggle-off" aria-hidden="true"/>
                </div>
                <div className="float-right">
                    <h6 className="preview">Preview</h6>
                </div>
                <button className="btn btn-success float-right">
                    Save
                </button>
            </div>
        </div>
    </div>

export default SavePreviewWidgetComponent