import React from "react";
import {Link} from "react-router-dom";
import courseService from '../services/CourseService'
import "../style/CourseTableComponentStyle.css"

export default class CourseRowComponent extends React.Component {
    state = {
        editing: false,
        course: this.props.course,
        selectRow: false
    }

    setEditing = (e, editing) => {
        if (e) {
            //e.preventDefault()
            e.stopPropagation()
        }
        this.setState({editing: editing})
    }

    ok = () =>
        courseService.updateCourse(
            this.state.course._id,
            this.state.course)
            .then(() => this.setEditing(false))

    updateCourseTitle = (newTitle) =>
        this.setState(prevState => ({
            course: {
                ...prevState.course,
                title: newTitle
            }
        }))

    toggleRow = () => {
        if (!this.state.editing) {
            const currentState = this.state.selectRow;
            this.setState({selectRow: !currentState});
        }

    }

    render() {
        return (
            <tr onClick={this.toggleRow}
                className={this.state.selectRow ? 'wbdv-select-row  ' : 'wbdv-row-hover'}>
                <td>
                    {
                        this.state.selectRow &&
                        !this.state.editing &&
                        <Link to={`/editor/${this.state.course._id}`}>
                            <div className={"wbdv-file-text-select"}>
                                <i className={"fa fa-file-text"}/>
                            </div>
                            <div className={"wbdv-course-title-select"}>
                                {this.state.course.title}
                            </div>
                        </Link>
                    }

                    {
                        !this.state.selectRow &&
                        !this.state.editing &&
                        <Link to={`/editor/${this.state.course._id}`}>
                            <div className={"wbdv-file-text"}>
                                <i className={"fa fa-file-text"}/>
                            </div>
                            <div className={"wbdv-course-title"}>
                                {this.state.course.title}
                            </div>
                        </Link>
                    }
                    {
                        this.state.editing &&
                        <input
                            className="form-control"
                            onChange={(event) => this.updateCourseTitle(event.target.value)}
                            value={this.state.course.title}/>
                    }
                </td>
                <td>
                    {
                        !this.state.selectRow &&
                        <div className={"wbdv-owned-by"}>{this.state.course.owner}</div>
                    }
                    {
                        this.state.selectRow &&
                        <div className={"wbdv-owned-by-select"}>{this.state.course.owner}</div>
                    }
                    </td>
                <td>
                    {
                      !this.state.selectRow &&
                      <div className={"wbdv-last-modified"}>{this.state.course.modified}</div>
                    }
                    {
                        this.state.selectRow &&
                        <div className={"wbdv-last-modified-select"}>{this.state.course.modified}</div>
                    }
                </td>
                <td>
                    {
                        !this.state.selectRow && !this.state.editing
                    }
                    {
                        this.state.selectRow &&
                        !this.state.editing &&
                        <span className={"float-right"}>
                    <button
                        className="btn wbdv-edit-row"
                        onClick={(e) => this.setEditing(e,true)}>
                        <i className={"fa fa-pencil fa-2x"}/>
                    </button>
                    <button
                        className={"btn wbdv-delete-row"}
                        onClick={
                            () => this.props.deleteCourse(this.props.course)}>
                        <i className={"fa fa-trash fa-2x"}/>
                    </button>
                </span>


                    }
                    {
                        this.state.editing &&
                        <span className={"float-right"}>
              <button className={"btn wbdv-confirm-change"}
                      onClick={this.ok}>
                <i className={"fa fa-check fa-2x"}/>
              </button>
            </span>
                    }
                </td>
            </tr>
        )
    }
}
