const findAllTopics = () => {
    console.log("Finding All topics")

    return fetch("https://wbdv-generic-server.herokuapp.com/api/001697547/topics")
        .then(response => response.json())

}

const deleteTopic = (topicId) => {
    console.log("Deleting topic")

    return fetch(`https://wbdv-generic-server.herokuapp.com/api/001697547/topics/${topicId}`, {
        method: 'DELETE'
    })
        .then(response => response.json())
}

const createTopic = (topic) => {
    console.log(topic)

    return fetch("https://wbdv-generic-server.herokuapp.com/api/001697547/topics", {
        method: 'POST',
        body: JSON.stringify(topic),
        headers: {
            'content-type': 'application/json'
        }
    })
        .then(response => response.json())

}


const updateTopic = (topicId, newTopic) => {
    console.log("Updating topic")
    return fetch(`https://wbdv-generic-server.herokuapp.com/api/001697547/topics/${topicId}`, {
        method: 'PUT',
        body: JSON.stringify(newTopic),
        headers: {
            'content-type': 'application/json'
        }

    })
        .then(response => response.json())
}


export default {
    findAllTopics,
    deleteTopic,
    createTopic,
    updateTopic
}