import React from "react";

class LessonTabsComponent extends React.Component {
    state = {
        newLessonTitle: 'Enter lesson',
        editingLesson: {}
    }

    componentDidMount() {
        this.props.findAllLessons()
    }

    render() {
        return(
            <ul className="nav nav-pills nav-fill">
                {
                    this.props.lessons.map(lesson =>
                        <div>
                            <li className={"nav-item wbdv-lesson-tabs"}
                                key={lesson._id}>
                                {
                                    this.state.editingLesson._id === lesson._id &&
                                    <div className={"row"}>
                                        <input className={"form-control col-6 col-xl-7"}
                                               onChange={(e) => {
                                                   const newTitle = e.target.value
                                                   this.setState(prevState => ({
                                                       editingLesson: {
                                                           ...prevState.editingLesson,
                                                           title: newTitle
                                                       }
                                                   }))}}
                                               value={this.state.editingLesson.title}/>
                                        <div className={"col-6 col-xl-5"}>
                                            <button className={"btn wbdv-delete-lesson"}
                                                    onClick={() => this.props.deleteLesson(lesson._id)}>
                                                <i className="fa fa-times"/>
                                            </button>
                                            <button className={"btn wbdv-save-lesson"}
                                                    onClick={() => {
                                                        this.props.updateLesson(this.state.editingLesson._id, this.state.editingLesson)
                                                        this.setState({editingLesson: {}})
                                                    }}>
                                                <i className="fa fa-check"/>
                                            </button>
                                        </div>

                                    </div>
                                }
                                {
                                    this.state.editingLesson._id !== lesson._id &&
                                    <span>
                                        <span>{lesson.title}</span>
                                        <button className={"btn wbdv-edit-lesson float-right"}
                                                onClick={() => this.setState({editingLesson: lesson})}>
                                            <i className="fa fa-pencil"/>
                                        </button>
                                        </span>

                                }

                            </li>
                        </div>
                    )
                }
                <div className={"row wbdv-add-lesson"}>
                    <span className={"col-2"}/>
                    <span className={"col-8"}>
                        <input className={"form-control float-right"}
                               onChange={(event) =>
                                   this.setState({
                                       newLessonTitle: event.target.value
                                   })}
                               value={this.state.newLessonTitle}/>
                    </span>
                    <span className={"col-2"}>
                        <button className={"btn wbdv-add-lesson-btn float-right"}
                                onClick={() => this.props.createLesson({
                                    title: this.state.newLessonTitle
                                })}>
                            <i className={"fa fa-plus"}/>
                        </button>
                    </span>

                </div>
            </ul>

        )
    }
}

export default LessonTabsComponent