import React from "react";
import {Link} from "react-router-dom";

const ProfileComponent = () =>
    <div className="container">
        <div>

            <div className="row">
                <h1 className="col-6">
                    Profile
                </h1>
            </div>


            <div className="form-group row">
                <div className="col-sm-4 col-md-3 col-lg-2"/>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <div className="alert alert-success wbdv-message" role="alert">
                        Profile successfully saved
                    </div>
                </div>
            </div>

            <div className="form-group row">
                <label htmlFor="username"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Username
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <input className="form-control wbdv-field wbdv-username"
                           id="username"
                           type="text"
                           placeholder="arizzo1234"
                           readOnly
                    />
                </div>
            </div>

            <div className="form-group row">
                <label htmlFor="phone"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Phone
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <input type="tel"
                           className="form-control wbdv-field wbdv-phone"
                           id="phone"
                           placeholder="(508)123-4567"/>
                </div>
            </div>

            <div className="form-group row">
                <label htmlFor="email"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Email
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <input type="email"
                           className="form-control wbdv-field wbdv-password"
                           id="email"
                           placeholder="arizzo1234@gmail.com"/>
                </div>
            </div>


            <div className="form-group row">
                <label htmlFor="role"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Role
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <select id="role"
                            className="form-control">
                        <option value="FACULTY">Faculty</option>
                        <option value="STUDENT">Student</option>
                        <option value="ADMIN">Admin</option>
                    </select>
                </div>
            </div>

            <div className="form-group row">
                <label htmlFor="dob"
                       className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                    Email
                </label>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <input type="date"
                           className="form-control wbdv-field wbdv-password"
                           id="dob"/>
                </div>
            </div>

            <div className="form-group row">
                <div className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                </div>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <button className="btn btn-block btn-primary wbdv-button wbdv-update">
                        Update
                    </button>
                </div>
            </div>

            <div className="form-group row">
                <div className="col-sm-4 col-md-3 col-lg-2 col-form-label">
                </div>
                <div className="col-sm-8 col-md-9 col-lg-10">
                    <Link to={'/'}
                          className="btn btn-block btn-primary wbdv-button wbdv-logout">
                        Logout
                    </Link>
                </div>
            </div>
        </div>
    </div>


export default ProfileComponent