import React from "react";
import {Link} from "react-router-dom";
import "../style/CourseEditorComponentStyle.css"
import SavePreviewWidgetComponent from "./SavePreviewWidgetComponent";
import AddWidgetComponent from "./AddWidgetComponent";
import ModuleListContainer from "../containers/ModuleListContainer";
import LessonTabsContainer from "../containers/LessonTabsContainer";
import WidgetListComponent from "./WidgetListComponent";
import TopicContainer from "../containers/TopicContainer";

// stateless component
const CourseEditorComponent = ({match}) => {
    return (
        <div>
            <div className={"row wbdv-editor-header"}>
                <div className={"col-1"}>
                    <Link to="/table/courses">
                        <button className={"btn wbdv-editor-back"}>
                            <i className={"fa fa-times"}/>
                        </button>
                    </Link>
                </div>

                <div className={"col-3"}>
                    <h4>Course Editor</h4>
                </div>

                <div className={"col-7"}>
                    <LessonTabsContainer {...match}/>
                </div>

            </div>


            <div className="row">
                <div className={"col-4"}>
                    <ModuleListContainer {...match}/>
                </div>

                <div className="col-8">

                    <TopicContainer {...match}/>
                    <br/>
                    <SavePreviewWidgetComponent/>
                    <br/>
                    {/*<WidgetListComp/>*/}
                    <WidgetListComponent/>
                    <br/>
                    <AddWidgetComponent/>
                    <br/>

                </div>
            </div>
        </div>
    )
}

export default CourseEditorComponent
