const findAllModules = () => {
    return fetch("https://wbdv-generic-server.herokuapp.com/api/001697547/modules")
        .then(response => response.json())
}

const deleteModule = (moduleId) => {
    return fetch(`https://wbdv-generic-server.herokuapp.com/api/001697547/modules/${moduleId}`, {
        method: 'DELETE'
    })
        .then(response => response.json())
}

const createModule = (course) =>
    fetch("https://wbdv-generic-server.herokuapp.com/api/001697547/modules", {
        method: 'POST',
        body: JSON.stringify(course),
        headers: {
            'content-type': 'application/json'
        }
    })
        .then(response => response.json())

const updateModule = (moduleId, newModule) =>
    fetch(`https://wbdv-generic-server.herokuapp.com/api/001697547/modules/${moduleId}`, {
        method: 'PUT',
        body: JSON.stringify(newModule),
        headers: {
            'content-type': 'application/json'
        }

    })
        .then(response => response.json())

export default {
    findAllModules,
    deleteModule,
    createModule,
    updateModule
}