import React from "react";
import CourseRowComponent from "./CourseRowComponent";
import "../style/CourseTableComponentStyle.css";

export default class CourseTableComponent extends React.Component
{
  render() {
    return(
      <div>
        <table className="table">
          <thead>
            <tr>
              <th className={"col-8 col-sm-5 col-md-4"}>
                <div>
                  Title
                  <i className={"fa fa-sort-up"}/>
                </div>
              </th>
              <th>
                <div className={"wbdv-owned-by"}>
                  Owned by
                </div>
              </th>
              <th>
                <div className={"wbdv-last-modified"}>
                  Last Modified
                </div>
              </th>
              <th>
                <button className={"btn float-right"}
                        onClick={
                  () => {
                    this.props.layout('grid')

                  }}>
                  <i className={"fa fa-th fa-lg"}/>
                </button>
                <button className={"btn float-right"}>
                  <i className={"fa fa-sort-alpha-asc"}/>
                </button>
              </th>
            </tr>
          </thead>
          <tbody>
          {
            this.props.courses.map(course =>
              <CourseRowComponent
                deleteCourse={this.props.deleteCourse}
                key={course._id}
                course={course}
                />
          )}
          </tbody>
        </table>
      </div>
    )
  }
}
