import React from "react";

const initialState = {
    newModuleTitle: 'Some title',
    modules: []
}

const moduleReducer = (state=initialState, event) => {
    switch (event.type) {
        case "ADD_MODULE":
            // ...
            return {
                modules: [
                    ...state.modules,
                    event.newModule
                ]
            }

        case "DELETE_MODULE":
            return {
                modules: state.modules.filter(module => module._id !== event.moduleId)
            }
        case "MODULE_TITLE_CHANGED":
            return {
                ...state,
                newModuleTitle: event.newModuleTitle
            }

        case "FIND_ALL_MODULES":
            return {
                ...state,
                modules: event.modules
            }

        case "UPDATE_MODULE":
            return {
                ...state,
                modules: state.modules.map(
                    module => module._id === event.updatedModule._id ?
                        event.updatedModule : module)
            }

        default:
            return state;

    }
}

export default moduleReducer