import React from "react";
import "../style/CourseEditorComponentStyle.css"

const WidgetListComponent = () =>
    <div>
        <div className="widget-box">
            <div className="row widget-sub">
                <div className="col-5">
                    <h4>Heading Widget</h4>
                </div>
                <div className="col-7">
                    <button className="btn btn-danger float-right wbdv-module-item-delete-btn">
                        <i className="fa fa-times"/>
                    </button>

                    <div className="dropdown div-inline float-right">
                        <button className="btn btn-light dropdown-toggle" type="button" id="dropdownMenuButton"
                                aria-haspopup="true" aria-expanded="false">
                            Heading
                        </button>
                        <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                            <a className="dropdown-item" href="#">Heading Text</a>
                            <a className="dropdown-item" href="#">Heading 1</a>
                            <a className="dropdown-item" href="#">Widget Name</a>
                        </div>
                    </div>

                    <button className="btn btn-warning float-right arrow">
                        <i className="fa fa-arrow-up"/>
                    </button>

                    <button className="btn btn-warning float-right arrow">
                        <i className="fa fa-arrow-down"/>
                    </button>
                </div>
            </div>

            <div className="row widget-sub widget-sub-border heading-text">
                <div>
                    <h5 className="widget-sub-header">Heading Text</h5>
                </div>
            </div>



                <div className="row widget-sub widget-sub-border">
                    <div className="dropdown div-inline float-right">
                        <button className="btn btn-light dropdown-toggle" type="button" id="heading-1-dropdown"
                                aria-haspopup="true" aria-expanded="false">
                            Heading 1
                        </button>
                        <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                            <a className="dropdown-item" href="#">Heading Text</a>
                            <a className="dropdown-item" href="#">Heading 1</a>
                            <a className="dropdown-item" href="#">Widget Name</a>
                        </div>
                    </div>
                </div>



                    <div className="row widget-sub widget-sub-border">
                        <div>
                            <h5 className="widget-sub-header">Widget name</h5>
                        </div>
                    </div>

                    <div className="row widget-sub">
                        <div>
                            <h4 className="widget-sub-header">Preview</h4>
                        </div>
                    </div>

                    <div className="row widget-sub">
                        <div>
                            <h3 className="widget-sub-header">Heading Text</h3>
                        </div>
                    </div>

        </div>
    </div>

export default WidgetListComponent
