const findAllLessons = () => {
    console.log("Finding All Lessons")

    return fetch("https://wbdv-generic-server.herokuapp.com/api/001697547/lessons")
        .then(response => response.json())

}

const deleteLesson = (lessonId) => {
    console.log("Deleting Lesson")

    return fetch(`https://wbdv-generic-server.herokuapp.com/api/001697547/lessons/${lessonId}`, {
        method: 'DELETE'
    })
        .then(response => response.json())
}

const createLesson = (lesson) => {
    console.log(lesson)

    return fetch("https://wbdv-generic-server.herokuapp.com/api/001697547/lessons", {
        method: 'POST',
        body: JSON.stringify(lesson),
        headers: {
            'content-type': 'application/json'
        }
    })
        .then(response => response.json())

}


const updateLesson = (lessonId, newLesson) => {
    console.log("Updating Lesson")
    return fetch(`https://wbdv-generic-server.herokuapp.com/api/001697547/lessons/${lessonId}`, {
        method: 'PUT',
        body: JSON.stringify(newLesson),
        headers: {
            'content-type': 'application/json'
        }

    })
        .then(response => response.json())
}


export default {
    findAllLessons,
    deleteLesson,
    createLesson,
    updateLesson
}