import React from "react";

class TopicComponent extends React.Component {
    state = {
        newTopicTitle: 'Enter topic',
        editingTopic: {}
    }

    componentDidMount() {
        this.props.findAllTopics()
    }

    render() {
        return(
            <ul className="nav nav-pills nav-fill">
                {
                    this.props.topics.map(topic =>
                        <div>
                            <li className={"nav-item wbdv-topic-tabs"}
                                key={topic._id}>
                                {
                                    this.state.editingTopic._id === topic._id &&
                                    <div className={"row"}>
                                        <input className={"form-control col-6 col-xl-7"}
                                               onChange={(e) => {
                                                   const newTitle = e.target.value
                                                   this.setState(prevState => ({
                                                       editingTopic: {
                                                           ...prevState.editingTopic,
                                                           title: newTitle
                                                       }
                                                   }))}}
                                               value={this.state.editingTopic.title}/>
                                        <div className={"col-6 col-xl-5"}>
                                            <button className={"btn wbdv-delete-topic"}
                                                    onClick={() => this.props.deleteTopic(topic._id)}>
                                                <i className="fa fa-times"/>
                                            </button>
                                            <button className={"btn wbdv-save-topic"}
                                                    onClick={() => {
                                                        this.props.updateTopic(this.state.editingTopic._id, this.state.editingTopic)
                                                        this.setState({editingTopic: {}})
                                                    }}>
                                                <i className="fa fa-check"/>
                                            </button>
                                        </div>

                                    </div>
                                }
                                {
                                    this.state.editingTopic._id !== topic._id &&
                                    <span>
                                        <span>{topic.title}</span>
                                        <button className={"btn wbdv-edit-topic float-right"}
                                                onClick={() => this.setState({editingTopic: topic})}>
                                            <i className="fa fa-pencil"/>
                                        </button>
                                        </span>

                                }

                            </li>
                        </div>
                    )
                }
                <div className={"row wbdv-add-topic"}>
                    <span className={"col-2"}/>
                    <span className={"col-8"}>
                        <input className={"form-control float-right"}
                               onChange={(event) =>
                                   this.setState({
                                       newTopicTitle: event.target.value
                                   })}
                               value={this.state.newTopicTitle}/>
                    </span>
                    <span className={"col-2"}>
                        <button className={"btn wbdv-add-topic-btn float-right"}
                                onClick={() => this.props.createTopic({
                                    title: this.state.newTopicTitle
                                })}>
                            <i className={"fa fa-plus"}/>
                        </button>
                    </span>

                </div>
            </ul>

        )
    }
}

export default TopicComponent