import React from "react";

class ModuleListComponent extends React.Component {
    state = {
        newModuleTitle: 'Enter Module',
        editingModule: {}
    }

    componentDidMount() {
        this.props.findAllModules()
    }

    render() {
        return(
            <div className={"wbdv-module-list-div"}>
                <div className="wbdv-module-section">
                    <ul className="list-group">
                        {
                            this.props.modules.map(module =>
                                <div className="row wbdv-module-item">
                                    <li className={"list-group-item wbdv-module-item-tile"}
                                        key={module._id}>
                                        {
                                            this.state.editingModule._id === module._id &&
                                                <div className={"row"}>
                                                    <input className={"form-control col-6 col-xl-7"}
                                                        onChange={(e) => {
                                                        const newTitle = e.target.value
                                                        this.setState(prevState => ({
                                                        editingModule: {
                                                            ...prevState.editingModule,
                                                            title: newTitle
                                                        }
                                                    }))}}
                                                           value={this.state.editingModule.title}/>
                                                   <div className={"col-6 col-xl-5"}>
                                                        <button className={"btn wbdv-delete-module float-right"}
                                                               onClick={() => this.props.deleteModule(module._id)}>
                                                            <i className="fa fa-times"/>
                                                        </button>
                                                        <button className={"btn wbdv-save-module float-right"}
                                                            onClick={() => {
                                                            this.props.updateModule(this.state.editingModule._id, this.state.editingModule)
                                                            this.setState({editingModule: {}})
                                                        }}>
                                                            <i className="fa fa-check"/>
                                                        </button>
                                                   </div>

                                                </div>
                                        }
                                        {
                                            this.state.editingModule._id !== module._id &&
                                                <span>
                                                    <span>{module.title}</span>
                                                <button className={"btn wbdv-edit-module float-right"}
                                                    onClick={() => this.setState({editingModule: module})}>
                                                    <i className="fa fa-pencil"/>
                                                </button>
                                                </span>

                                        }

                                    </li>
                                </div>
                            )
                        }
                        <div className={"row wbdv-add-module"}>
                            <span className={"col-8"}>
                                <input className={"form-control"}
                                       onChange={(event) =>
                                           this.setState({
                                               newModuleTitle: event.target.value
                                           })}
                                       value={this.state.newModuleTitle}/>
                            </span>
                            <span className={"col-4"}>
                                <button className={"btn wbdv-add-module-btn"}
                                        onClick={() => this.props.createModule({
                                            title: this.state.newModuleTitle
                                        })}>
                                    <i className={"fa fa-plus"}/>
                                </button>
                            </span>

                        </div>
                    </ul>

                </div>
            </div>
        )
    }
}

export default ModuleListComponent