import React from "react";
import CourseCardComponent from "./CourseCardComponent";

export default class CourseGridComponent
    extends React.Component {

    render() {
        return (
            <div className={"wbdv-grid-spacing"}>
                <div className={"row wbdv-grid-heading"}>
                    <div className={"col-6"}>
                        <h4>Recent Documents</h4>
                    </div>

                    <div className={"col-4"}>
                        <h6 className={"wbdv-grid-owner"}>
                            Owned by me
                            <i className={"fa fa-sort-down wbdv-grid-sort"}/>
                        </h6>

                    </div>
                    <div className={"col-2"}>
                        <button className={"btn float-right"}
                                onClick={
                                    () => this.props.layout('table')}>
                            <i className={"fa fa-list fa-lg"}/>
                        </button>
                    </div>


                </div>

                <div className="row">
                    {this.props.courses.map(course =>
                        <CourseCardComponent
                            deleteCourse={this.props.deleteCourse}
                            key={course._id}
                            course={course}
                        />)}
                </div>


            </div>
        )
    }
}
