import React from "react";
import CourseTableComponent from "../components/CourseTableComponent";
import CourseGridComponent from "../components/CourseGridComponent";
import courseService from "../services/CourseService"
import "../style/CourseManagerContainerStyle.css"
import {Link} from "react-router-dom";

class CourseManagerContainer
    extends React.Component
{
    state = {
        layout: this.props.match.params.layout,
        courses: [],
        newCourseTitle: 'New Title ABC'
    }

    componentDidMount() {
        courseService.findAllCourses()
            .then(actualArrayOfCourses =>
                this.setState({
                    courses: actualArrayOfCourses
                }))

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.match.params.layout !== this.props.match.params.layout) {
            this.setState({
                layout: this.props.match.params.layout

            })
        }
    }

    setLayout = (layout) => {
        this.props.history.push(`/${layout}/courses`)
    }

    deleteCourse = (courseToDelete) =>
        courseService.deleteCourse(courseToDelete._id)
            .then(() => this.setState(prevState => ({
                courses: prevState
                    .courses.filter(course => course !== courseToDelete)
            })))

    addCourse = (title) =>
        courseService.createCourse({
            title: title,
            owner: 'me',
            modified: (new Date()).toDateString()
        })
            .then(theActualNewCourse =>
                this.setState((prevState) => {
                    return {
                        courses: [
                            ...prevState.courses,
                            theActualNewCourse
                        ]
                    }
                }))

    render() {

        return(

            <div>
                <div className={"navbar row fixed-top navbar-dark bg-primary"}>
                    <div className="row col-1 col-sm-1 col-md-4 col-lg-4">
                        <Link to={'/'}>
                            <button className={"btn wbdv-hamburger"}>
                                <i className={"fa fa-bars fa-2x"}/>
                            </button>
                        </Link>

                        <h2 className={"wbdv-course-manager"}>Course Manager</h2>
                    </div>

                    <div className={"row wbdv-add-course-group col-9 col-sm-10 col-md-8 col-lg-8"}>
                        <input className={"form-control wbdv-input"}
                            onChange={(event) => this.setState({
                                newCourseTitle: event.target.value
                            })}
                            value={this.state.newCourseTitle}
                            placeholder="Course Title"/>

                        <button className={"btn btn-success wbdv-add"}
                                onClick={
                                () => this.addCourse(this.state.newCourseTitle)}>
                                <i className={"fa fa-plus"}/>
                        </button>
                    </div>

                </div>

                {
                    this.state.layout === 'table' &&
                    <div>
                        <CourseTableComponent
                            deleteCourse={this.deleteCourse}
                            courses={this.state.courses}
                            layout={this.setLayout}/>
                    </div>
                }
                {
                    this.state.layout === 'grid' &&
                    <div>
                            <CourseGridComponent
                                deleteCourse={this.deleteCourse}
                                courses={this.state.courses}
                                layout={this.setLayout}
                            />


                    </div>
                }
            </div>
        )
    }
}

export default CourseManagerContainer