import React from "react";

const initialState = {
    newTopicTitle: 'Some title',
    topics: []
}

const topicReducer = (state=initialState, event) => {
    switch (event.type) {
        case "ADD_TOPIC":
            // ...
            return {
                topics: [
                    ...state.topics,
                    event.newTopic
                ]
            }

        case "DELETE_TOPIC":
            return {
                topics: state.topics.filter(topic => topic._id !== event.topicId)
            }
        case "topic_TITLE_CHANGED":
            return {
                ...state,
                newTopicTitle: event.newTopicTitle
            }

        case "FIND_ALL_TOPICS":
            return {
                ...state,
                topics: event.topics
            }

        case "UPDATE_TOPIC":
            return {
                ...state,
                topics: state.topics.map(
                    topic => topic._id === event.updatedTopic._id ?
                        event.updatedTopic : topic)
            }

        default:
            return state;

    }
}

export default topicReducer