import React from "react";

const initialState = {
    newLessonTitle: 'Some title',
    lessons: []
}

const lessonReducer = (state=initialState, event) => {
    switch (event.type) {
        case "ADD_LESSON":
            // ...
            return {
                lessons: [
                    ...state.lessons,
                    event.newlesson
                ]
            }

        case "DELETE_LESSON":
            return {
                lessons: state.lessons.filter(lesson => lesson._id !== event.lessonId)
            }
        case "LESSON_TITLE_CHANGED":
            return {
                ...state,
                newLessonTitle: event.newlessonTitle
            }

        case "FIND_ALL_LESSONS":
            return {
                ...state,
                lessons: event.lessons
            }

        case "UPDATE_LESSON":
            return {
                ...state,
                lessons: state.lessons.map(
                    lesson => lesson._id === event.updatedLesson._id ?
                        event.updatedLesson : lesson)
            }

        default:
            return state;

    }
}

export default lessonReducer