import {connect} from "react-redux";
import LessonTabsComponent from "../components/LessonTabsComponent";
import LessonTabsService from "../services/LessonTabsService";

const stateToPropertyMapper = (state, ownProps) => {
    return {
        lessons : state.lessonReducer.lessons,
        newLessonTitle: state.lessonReducer.newLessonTitle,
        params: ownProps.params
    }
}

const dispatchToPropertyMapper = (dispatch) => {
    return {
        findAllLessons: () => {
            LessonTabsService.findAllLessons()
                .then(actualLessons => dispatch({
                    type: 'FIND_ALL_LESSONS',
                    lessons: actualLessons
                }))
        },
        createLesson: (newLesson) => {

            LessonTabsService.createLesson(newLesson)
                .then(actualNewLesson => dispatch({
                    type: "ADD_LESSON",
                    newLesson: actualNewLesson
                }))


        },
        deleteLesson: (lessonId) => {
            LessonTabsService.deleteLesson(lessonId)
                .then(status => dispatch({
                    type: "DELETE_LESSON",
                    lessonId: lessonId
                }))

        },
        updateLesson: (lessonId, newLesson) => {
            LessonTabsService.updateLesson(lessonId, newLesson)
                .then(status => dispatch({
                    type: 'UPDATE_LESSON',
                    updatedLesson: newLesson
                }))
        }
    }
}

const LessonTabsContainer = connect(stateToPropertyMapper, dispatchToPropertyMapper)(LessonTabsComponent)

export default LessonTabsContainer