import {connect} from "react-redux";
import ModuleListComponent from "../components/ModuleListComponent";
import ModuleService from "../services/ModuleService";

const stateToPropertyMapper = (state, ownProps) => {
    return {
        modules: state.moduleReducer.modules,
        newModuleTitle: state.moduleReducer.newModuleTitle,
        params: ownProps.params
    }
}

const dispatchToPropertyMapper = (dispatch) => {
    return {
        findAllModules: () => {
            ModuleService.findAllModules()
                .then(actualModules => dispatch({
                    type: 'FIND_ALL_MODULES',
                    modules: actualModules
                }))
        },
        createModule: (newModule) => {
            ModuleService.createModule(newModule)
                .then(actualNewModule => dispatch({
                    type: "ADD_MODULE",
                    newModule: actualNewModule
                }))

        },
        deleteModule: (moduleId) => {
            ModuleService.deleteModule(moduleId)
                .then(status => dispatch({
                    type: "DELETE_MODULE",
                    moduleId: moduleId
                }))

        },
        updateModule: (moduleId, newModule) => {
            ModuleService.updateModule(moduleId, newModule)
                .then(status => dispatch({
                    type: 'UPDATE_MODULE',
                    updatedModule: newModule
                }))
        }
    }
}

const ModuleListContainer = connect(stateToPropertyMapper, dispatchToPropertyMapper)(ModuleListComponent)

export default ModuleListContainer