import {connect} from "react-redux";
import topicTabsService from "../services/TopicService";
import TopicComponent from "../components/TopicComponent";

const stateToPropertyMapper = (state, ownProps) => {
    return {
        topics : state.topicReducer.topics,
        newTopicTitle: state.topicReducer.newTopicTitle,
        params: ownProps.params
    }
}

const dispatchToPropertyMapper = (dispatch) => {
    return {
        findAllTopics: () => {
            topicTabsService.findAllTopics()
                .then(actualTopics => dispatch({
                    type: 'FIND_ALL_TOPICS',
                    topics: actualTopics
                }))
        },
        createTopic: (newTopic) => {

            topicTabsService.createTopic(newTopic)
                .then(actualNewTopic => dispatch({
                    type: "ADD_TOPIC",
                    newTopic: actualNewTopic
                }))


        },
        deleteTopic: (topicId) => {
            topicTabsService.deleteTopic(topicId)
                .then(status => dispatch({
                    type: "DELETE_TOPIC",
                    topicId: topicId
                }))

        },
        updateTopic: (topicId, newTopic) => {
            topicTabsService.updateTopic(topicId, newTopic)
                .then(status => dispatch({
                    type: 'UPDATE_TOPIC',
                    updatedTopic: newTopic
                }))
        }
    }
}

const TopicContainer = connect(stateToPropertyMapper, dispatchToPropertyMapper)(TopicComponent)

export default TopicContainer