import React from "react";
import {Link} from "react-router-dom";

const LoginComponent = () =>
    <div className="container">
        <div>
            {
                //<!--Header-->
            }
            <div className="row">
                <h1 className="col-6">
                    Sign In
                </h1>
                <div className="col-6">
                    <Link to="/"
                       className="wbdv-cancel-link float-right">
                        Cancel</Link>
                </div>
            </div>

            {
                //<!--Username-->
            }
            <div className="form-group row">
                <label htmlFor="username"
                       className="col-sm-2 col-form-label">
                    Username </label>
                <div className="col-sm-10">
                    <input className="form-control wbdv-field wbdv-username"
                           id="username"
                           placeholder="arizzo1234"/>
                </div>
            </div>

            {
                //<!--Password-->
            }
            <div className="form-group row">
                <label htmlFor="password"
                       className="col-sm-2 col-form-label">
                    Password </label>
                <div className="col-sm-10">
                    <input type="password"
                           className="form-control wbdv-field wbdv-password"
                           id="password"
                           placeholder="123qWe!@#"/>
                </div>
            </div>

            {
                //<!--Action Items-->
            }
            <div className="form-group row">
                <label className="col-sm-2 col-form-label"/>
                <div className="col-sm-10">
                    {
                        //<!--Sign In Button-->
                    }
                    <Link to="/profile"
                          className="wbdv-cancel-link float-right btn btn-block btn-primary wbdv-button wbdv-login">
                        Sign In
                    </Link>


                    <div className="row">
                        {
                            //<!--Forgot Password-->
                        }
                        <div className="col-6 wbdv-link wbdv-forgot-password">
                            <a href="#">
                                Forgot Password?
                            </a>
                        </div>

                        {
                            //<!--Register-->
                        }
                        <div className="col-6 wbdv-link wbdv-register">
                            <Link to={'/register'}
                            className={"float-right wbdv-link"}>
                                Not registered yet?
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>


export default LoginComponent
